import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Productsfetch } from './product'; 
import { Observable } from 'rxjs';

const httpOptions = {
  headers: new HttpHeaders({'Content-Type' : 'application/json'})
}

@Injectable({
  providedIn: 'root'
})
export class DataService {

  constructor(private http: HttpClient) { }
  
     getProducts():Observable<Productsfetch[]>{

       return this.http.get<Productsfetch[]>('/api/products');
     }
     getbypricelow():Observable<Productsfetch[]>{

      return this.http.get<Productsfetch[]>('/api/products/filterbylow');
    }
    getbypricehigh():Observable<Productsfetch[]>{

      return this.http.get<Productsfetch[]>('/api/products/filterbyhigh');
    }





}


